<template>
  <div>
    <van-steps direction="vertical" :active="active">
      <van-step v-for="(item, index) in list" :key="index">
        <h3>{{ item.content }}</h3>
        <p>{{ item.timestamp }}</p>
        <p v-if="item.type === 'image' && item.value">
          <van-image
            v-for="(it, i) in item.value"
            :key="i"
            width="100"
            height="100"
            :src="$api.baseImage + it"
            @click="preview(item.value, i)"
          />
        </p>
        <p v-else-if="item.type === 'attachs' && item.value">
          <a v-for="(it, i) in item.value" :key="i" :href="$api.baseImage + it" target="_blank" /> 文件
        </p>
        <p v-else-if="item.type === 'checkbox' && item.value">
          {{ item.value.join(',') }}
        </p>
        <p v-else>
          {{ item.value }}
        </p>
      </van-step>
    </van-steps>
  </div>
</template>

<script>
import { ImagePreview } from 'vant'
export default {
  name: 'TicketTaskDetail',
  data() {
    return {
      id: '',
      state: '',
      list: [],
      active: 0
    }
  },
  mounted() {
    this.id = this.$route.query.id
    this.state = this.$route.query.state
    this.getDetailData()
  },
  methods: {
    async getDetailData() {
      this.list = await this.$post('/task/ticket/detail', { id: this.id })
      if (parseInt(this.state) === 10) {
        this.active = this.list.length - 1
      }
    },
    preview(images, index) {
      const list = []
      images.forEach(it => {
        list.push(this.$api.baseImage + it)
      })
      ImagePreview({
        images: list,
        startPosition: index
      })
    }
  }
}
</script>

